import styles from "./PopularUserList.module.scss";
import PopularUserCard from "./PopularUserCard";
import { useAuth } from "../../../Context/AuthContext"; // id context
import { useEffect } from "react";
import { getTopFollower } from "../../../Api/UserAPI"; //10API
import { useFollowBtn } from "../../../Context/ProfileContext"; //context

function PopularUserList() {
  const { topFollower, setTopFollower } = useFollowBtn(); //context
  // 
  const currentUserInfo = useAuth().currentUser;
  const currentUserID = currentUserInfo.id;
  // APITop Follower (mountuseEffect)
  useEffect(() => {
    // fetch api
    const apiTweets = async () => {
      try {
        const apiTopFollowers = await getTopFollower(); // 
        setTopFollower(apiTopFollowers);
      } catch (error) {
        console.error("initialize TopFollowers error", error);
      }
    };
    apiTweets();
  }, [setTopFollower]);

  // PopularUserCardmap
  return (
    <div className={styles["container"]}>
      <h4 className={styles["popular__title"]}></h4>
      <div className={styles["popular__user-list"]}>
        {topFollower &&
          topFollower?.map((user) => (
            <PopularUserCard
              key={user?.id}
              userID={user?.id}
              accountName={user?.account}
              userName={user?.name}
              isFollowed={user?.isFollowing}
              avatar={user?.avatar}
              currentUserID={currentUserID}
              setTopFollower={setTopFollower}
            />
          ))}
      </div>
    </div>
  );
}
export default PopularUserList;
