//
export const userTweet = async (text) => {
  try {
    const res = await axiosInstance.post(`${baseUrl}/api/tweets`,{description:text})
    return res;
  } catch (error) {
    console.error("[Submit Tweet Failed]:", error);
    return error;
  }
};

